<template>
  <div class="product-detail">
  <div class="product-calculation"  v-if="tbShow">
    <p class="close" @click="tbShow=false""><img src="../../assets/images/close.png" alt=""></p>
    <div class="content">
      <div class="layer ">
        <div>投保人</div>
        <div>{{userInfo.name}}</div>
      </div>
      <div class="layer ">
        <div>为谁投保</div>
        <div>{{insuredName}}</div>
        <!--<div class=" icon_insure" @click="insuredShow=true">{{insuredName}}</div>-->
      </div>
      <div class="layer ">
        <div>保险期间</div>
        <div>1天</div>
      </div>
      <div class="layer ">
        <div>生效日期</div>
        <div @click="open('startPicker')" class="date"><span>{{startDate}}</span><i
          :style="startDateShow ? upImg : downImg "></i></div>
      </div>
      <mt-datetime-picker
        ref="startPicker"
        type="date"
        :closeOnClickModal="false"
        :startDate="new Date(this.startDatefault)"
        year-format="{value} 年"
        month-format="{value} 月"
        date-format="{value} 日"
        @cancel="changeStartCancel"
        @confirm="changeStartDate">
      </mt-datetime-picker>
      <div class="layer ">
        <div>终止日期</div>
        <div>{{endDate}}</div>
      </div>
      <div class="layer ">
        <div>计划选择</div>
        <ul class=" plan-name">
          <li v-for="(item,index) in plans" :key="index" @click="changePlan(index)"
              :class="currentPlanIndex==index?'active':''">{{item.plan_name}}
          </li>
        </ul>
      </div>
      <div class="plan-con">
        <p class="ins">保障方案</p>
        <ul class="plans-con-wrapper">
          <li v-for="(plancon,index) in planCons" v-show=" index == currentPlanIndex" :key="index">
            <p class="item" v-for="(item,index) in plancon" :key="index">
              <span>{{item.name}}</span>
              <span>{{item.value}}</span>
            </p>
          </li>
        </ul>
      </div>
    </div>
    <div class="insured-pop" v-if="insuredShow" id="insured">
      <i class="close" @click="insuredShow=false"></i>
      <ul>
        <li class="clearfix" @click="seletcInsured(item)" v-for="(item,index) in insuredList" :key="index">
          <span class="name">{{item.name}}</span>
          <span class="relation">{{getRelation(item.type)}}</span>
          <span class="idnum">{{item.id_num}}</span>
        </li>
      </ul>
      <div class="add-btn-wrapper clearfix">
        <span @click="addInsuredShow=true">新增被保险人</span>
      </div>
    </div>

    <!--<insuredAdd-->
    <!--:isShow="addInsuredShow"-->
    <!--@cancel="addInsuredShow=false"-->
    <!--&gt;</insuredAdd>-->
  </div>
   <div class="cover"  v-show="tbShow&&!timePickShow" @click="tbShow=false"></div>
  <detail-common @goInsured="submit" :price="totalPrice"></detail-common>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
import moment from 'moment'
import { getRelation } from '@/assets/js/mixins/base.js'
import detailCommon from '@/components/product/TheDetailCommon'
import { getProductDetail } from '@/assets/js/mixins/productDetail.js'
// import insuredAdd from '@/components/TheInsured'

export default {
  name: 'TheDetailCalculation',
  mixins: [getRelation, getProductDetail],
  components: { detailCommon },
  data () {
    return {
      timePickShow: false,
      startDateShow: false,
      amemdPlanId: '',
      amendBbxrName: '',
      addInsuredShow: false,
      createDateShow: false,
      insuredShow: false,
      plans: [],
      planCons: [],
      startDate: moment(new Date()).add(1, 'days').format('YYYY-MM-DD'),
      endDate: moment(new Date()).add(1, 'days').format('YYYY-MM-DD'),
      calculationFactors: [],
      plan_choice: '',
      totalPrice: '',
      planDefault: '',
      insuredList: {},
      bbxrName: '',
      insured: {
        relationId: '',
        skuId: '',
        planId: ''
      }
    }
  },
  mounted () {
    if (!this.$route.query.orderSn) {
      this.getPlans()
      this.getCalculationFactors()
    }
    if (this.$route.query.fromApplicant) {
      this.tbShow = true
    }
    if (this.$route.query.orderSn) {
      this.tbShow = true
      this.getCalculationFactors().then(res => {
        if (res) {
          this.amendOrder().then(res => {
            if (res) {
              this.getPlans()
            }
          })
        }
      })
    }
  },
  methods: {
    submit () {
      if (!this.tbShow) {
        this.tbShow = true
        return false
      }
      let postData = {
        skuId: this.insured.skuId,
        planId: this.insured.planId,
        productId: this.$route.query.productId,
        relationId: this.insured.relationId,
        effDate: this.startDate,
        matuDate: this.endDate
      }
      this.$api.product.orderSubnit(postData).then(res => {
        if (res.code === 1) {
          this.$router.push({
            path: '/product/orderConfirm',
            query: {
              sn: res.data,
              productId: this.$route.query.productId
            }
          })
        } else {
          this.$toast.center(res.message)
        }
      })
    },
    getCalculationFactors () {
      return new Promise((resolve, reject) => {
        let postData = {
          productId: this.$route.query.productId
        }
        this.$api.product.getCalculationFactors(postData).then(res => {
          if (res.code === 1) {
            resolve(res)
            this.calculationFactors = res.data
            for (let i = 0; i < res.data.length; i++) {
              if (res.data[i].is_default) {
                this.currentPlanIndex = i
                this.plan_choice = res.data[i].plan_id
              }
            }
            this.matchPrice(this.calculationFactors, this.plan_choice)
          }
        })
      })
    },
    matchPrice (data, nameId) {
      data.forEach((item) => {
        // console.log(item.plan_id, item.plan_id === nameId, item.calculation_factors, item.calculation_factors.length, item.calculation_factors[0].name === '天数')
        if (item.plan_id && item.plan_id === nameId &&
            item.calculation_factors && item.calculation_factors.length && item.calculation_factors[0].name === '天数') {
          this.totalPrice = item.sku_price
          this.insured.skuId = item.id
          this.insured.planId = item.plan_id
          return false
        }
      })
      return this.totalPrice
    },
    open (picker) {
      this.$refs[picker].open()
      this.startDateShow = true
      this.timePickShow = true
    },
    changeStartCancel () {
      this.startDateShow = false
      this.timePickShow = false
    },
    changeStartDate (val) {
      this.startDate = moment(val).format('YYYY-MM-DD')
      this.startDateShow = false
      this.timePickShow = false
    },
    seletcInsured (item) {
      this.bbxrName = item.name
      this.insuredShow = false
      this.insured.relationId = item.id
    }
  },
  watch: {
    startDate (val) {
      this.endDate = moment(val).format('YYYY-MM-DD')
    },
    plan_choice (val) {
      this.matchPrice(this.calculationFactors, val)
    }
  },
  computed: {
    insuredName () {
      if (this.bbxrName) {
        return this.bbxrName
      } else if (this.$route.query.orderSn) {
        return this.amendBbxrName
      } else {
        return this.userInfo.name
      }
    },
    ...mapGetters([
      'userInfo'
    ])
  }
}
</script>

<style  lang="scss">
  @import "@/assets/styles/productDetailCommon.scss";
  .v-modal{
    opacity: 1;
    background: rgba(28,29,29,0.7) !important;
  }
</style>
