<template>
  <div class="detail-top ">
      <p class="title">{{topList.name}}</p>
      <div class="company-logo">
        <img :src="baseSrc+topList.brandLogo" alt="公司logo">
      </div>
      <p class="condition">
        <span class="condition-item" v-for="(item,index) in topList.insure_condition" :key="index">{{item.name}}：{{item.value}}</span>
      </p>
      <p class="product-ts">
        <span class="ts-item" v-if="topList.feature1"><i></i>{{topList.feature1}}</span>
        <span class="ts-item" v-if="topList.feature2"><i></i>{{topList.feature2}}</span>
        <span class="ts-item" v-if="topList.feature3"><i></i>{{topList.feature3}}</span>
      </p>
      <div class="protocal-wrapper">
        <!--<span @click="goProtocalList"><img src="../../../assets/images/icon_clause.png" alt="">保险条款</span>-->
        <!--<span  @click="notifyShow=true"><img src="../../../assets/images/icon_notify.png" alt="" >客户告知书</span>-->
        <!--<span @click="goSample" v-if="topList.policy_sample_url"><img src="../../../assets/images/icon_sample.png" alt="">保单样本书</span>-->
      </div>
      <!--<div class="collect">-->
        <!--<i :style="collectImg" @click="collectOrNot"></i>收藏-->
      <!--</div>-->
    </div>
    <!--<pop-box   :title="'客户告知书'" v-if="notifyShow"-->
    <!--:width="874"-->
    <!--:height="wHeight - 200"-->
    <!--:content="notifyCon"-->
    <!--@dealClose="handleNoticyClose">></pop-box>-->
  </div>
</template>

<script>
// import popBox from '@/components/BasePopBox'
import { mapGetters } from 'vuex'
import { getLocalStorage } from '@/assets/js/localStorage'

export default {
  name: 'TheDetailTop',
  props: {
    topList: {
      type: Object,
      default: () => {
        return {}
      }
    },
    isFlag: {
      type: String,
      default: ''
    }
  },
  data () {
    return {
      notifyShow: false,
      sampleCon: '',
      baseSrc: '',
      isCollect: false,
      notifyCon: '',
      wHeight: ''
    }
  },
  mounted () {
    this.baseSrc = `${process.env.VUE_APP_RESOURCE_URL}`
    this.getNotify()
    this.wHeight = document.documentElement.clientHeight || document.body.clientHeight
    // 1未收藏 0 收藏
  },
  methods: {
    handleNoticyClose () {
      this.notifyShow = false
    },
    goProtocalList () {
      this.$router.push({
        path: '/product/protocal',
        query: {
          productId: this.$route.query.productId,
          productName: this.topList.name
        }
      })
    },
    goSample () {
      window.open(`${process.env.VUE_APP_RESOURCE_URL}/${this.topList.policy_sample_url}`)
    },
    getNotify () {
      let postData = {
        id: this.$route.query.productId
      }
      this.$api.product.getNoticeBook(postData).then(res => {
        this.notifyCon = res.data
      })
    },
    collectOrNot () {
      if (getLocalStorage('token')) {
        let postData = {
          productId: this.$route.query.productId
        }
        this.$api.personCenter.collectOrNot(postData).then(res => {
          if (res.code === 1) {
            this.isCollect = !this.isCollect
            if (this.isCollect) {
              this.$toast.center('已收藏')
            } else {
              this.$toast.center('已取消收藏')
            }
          }
        })
      } else {
        this.$router.push({
          name: 'SmsLogin',
          params: {
            redirctUrl: this.$route.path,
            productId: this.$route.query.productId
          }
        })
      }
    },
    toNewPage (url) {
      window.open(url)
    }
  },
  computed: {
    ...mapGetters(['token'])
    // collectImg () {
    //   return this.isCollect && this.token ? {
    //     'background-image':
    //     'url(' + require('@/assets/images/icon_collected.png') + ')'
    //   } : {
    //     'background-image':
    //     'url(' + require('@/assets/images/icon_collect.png') + ')'
    //   }
    // }
  },

  watch: {
    'isFlag': {
      handler (val) {
        this.isCollect = val === '0'
      }
    }
  },
  components: {
    // popBox
  }
}
</script>

<style scoped lang="scss">
  @import "@/assets/styles/mixin.scss";

  .detail-top {
    background: #fff;
    padding: pxToRem(29px) 0 0 pxToRem(30px);
    .title {
      color: $color-text;
      font-weight: bold;
      font-size: pxToRem(36px);
      width: pxToRem(560px);
      line-height: pxToRem(42px);
    }
    .condition {
      margin: pxToRem(30px) 0;
      .condition-item {
        font-size: pxToRem(24px);
        color: #A5A8A8;
        padding-right:pxToRem(30px);
        &:not(:last-child):after {
          content: '|';
          padding:0 pxToRem(10px);
        }
      }
    }
    .product-ts {
      font-size: 0;
      display: flex;
      flex-wrap: wrap;
      .ts-item {
        font-size: pxToRem(28px);
        color: $color-text;
        margin-right: pxToRem(50px);
        margin-bottom:pxToRem(20px);
        i {
          display: inline-block;
          vertical-align: top;
          margin-top: pxToRem(-2px);
          background: url("../../assets/images/icon_ts.png") no-repeat;
          background-size: pxToRem(28px) pxToRem(28px);
          width: pxToRem(28px);
          height: pxToRem(28px);
          margin-right: pxToRem(12px);
        }
      }

    }
    .company-logo {
      position: absolute;
      right: pxToRem(30px);
      top:  pxToRem(30px);
      width: pxToRem(110px);
      height: pxToRem(60px);
      overflow: hidden;
      img {
        width:pxToRem(110px);
        height:pxToRem(60px);
      }
    }

    .top-right {
      max-width: 374px;

      .protocal-wrapper {
        padding-right: 100px;
        margin-top: 72px;
        span {
          font-size: 12px;
          color: #3875E2;
          &:not(:last-child) {
            margin-right: 21px;
          }
          img {
            width: 16px;
            height: 16px;
            vertical-align: top;
            margin-right: 5px;
            padding-top: 1px;
          }
        }
      }
      .collect {
        position: absolute;
        top: 92px;
        right: 20px;
        i {
          display: inline-block;
          vertical-align: middle;
          width: 24px;
          height: 24px;
          background-repeat: no-repeat;
          margin: -5px 6px 0;
        }
      }
    }
  }

</style>
