<template>
  <div class="detail-common ">
    <detail-top :topList="detailData" :isFlag="isFlag"></detail-top>
    <div class="space"></div>
    <div class="detail-content ">
      <div class="content-wrapper">
        <detail-introduce :introduce="detailData" :parameters="parameters"></detail-introduce>
      </div>

      <!--<div ref="rightDetail" id="rightDetail" class="rightDetail" >-->
      <!--<detail-calculation v-show="tbShow" @close="tbShow=false"></detail-calculation>-->

      <!--<detail-applicant :price="price"  v-else></detail-applicant>-->
      <!--</div>-->
    </div>
    <div class="bottom">

      <p class="price" v-if="isLogin">{{parseFloat(price).toFixed(2)}}<span>元</span></p>
      <p class="price" v-else>{{parseFloat(price).toFixed(2)}}<span>元起</span></p>
      <div class="insured-btn" @click="goInsured">立即投保</div>
    </div>
  </div>
</template>

<script>
// import { mapGetters } from 'vuex'
import detailTop from '@/components/product/TheDetailTop'
import detailIntroduce from '@/components/product/TheDetailIntroduce'
import { getLocalStorage } from '@/assets/js/localStorage'

export default {
  name: 'TheDetailMain',
  props: {
    price: {
      type: String,
      defalut: ''
    }
  },
  data () {
    return {
      isLogin: false,
      detailData: {},
      searchBarFixed: false,
      memberId: '',
      productId: '',
      parameters: [],
      isFlag: ''
    }
  },
  mounted () {
    if (getLocalStorage('userInfo')) {
      this.memberId = getLocalStorage('userInfo').id
    }
    if (getLocalStorage('token')) {
      this.isLogin = true
    }
    this.getDetail()
  },
  methods: {
    goInsured () {
      if (getLocalStorage('token')) {
        // this.tbShow = true
        // this.sublime()
        this.$emit('goInsured')
      } else {
        this.$router.push({
          name: 'Applicant',
          query: {
            productId: this.$route.query.productId
          }
        })
      }
    },
    getDetail () {
      let postData = {
        id: this.$route.query.productId,
        memberId: this.memberId
      }
      this.$api.product.productDetail(postData).then(res => {
        this.detailData = res.data.product
        if (res.data.flag === '') {
          this.isFlag = '1'
        } else {
          this.isFlag = res.data.flag
        }
        //    this.price = res.data.product.price
        if (res.data.product.parameter_values !== null) {
          this.parameters = res.data.product.parameter_values[0].entries
          // this.parameters.forEach(item => {
          //    item.entries = item.entries.filter(item => item.index !== '')
          //  })
          // this.parameters = this.parameters.filter((item) => item.entries.length)
        }
      })
    }
  },
  components: {
    detailTop,
    detailIntroduce
  }
}
</script>

<style scoped lang="scss">
  @import "@/assets/styles/mixin.scss";

  .space {
    height: pxToRem(16px);
    background: #f5f5f5;
  }

  .bottom {
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    width: 100%;
    z-index: 104;
    display: flex;
    height: pxToRem(100px);
    line-height: pxToRem(100px);
    background: #FFFFFF;
    justify-content: space-between;
    /*box-shadow: 0 pxToRem(-4px) pxToRem(16px) 0 rgba(225,225,225,0.64);*/
    .price {
      flex: 1;
      font-size: pxToRem(54px);
      color: $color-active-text;
      text-align: center;
      span {
        font-size: pxToRem(24px);
        color: #D5D8D8;
      }
    }
    .insured-btn {
      width: pxToRem(286px);
      background-image: linear-gradient(-135deg, #21DED8 0%, #12D2CC 100%);
      border-radius: pxToRem(2px);
      font-size: pxToRem(36px);
      text-align: center;
      color: #FFFFFF;
    }
  }

</style>
